.App {
    width: 100%;
    height: 100%;
}
.Coming-soon-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
    background-image: url("../assets/images/main_background.jpg");

    font-size: 12rem;
    font-weight: 700;
    color: #ffffff;
    text-shadow: 5px 5px #465892;
    text-align: center;
    user-select: none;
}

@media only screen and (max-width: 700px) {
    .Coming-soon-wrapper {
        font-size: 10rem;
    }
}
@media only screen and (max-width: 600px) {
    .Coming-soon-wrapper {
        font-size: 8rem;
    }
}
@media only screen and (max-width: 450px) {
    .Coming-soon-wrapper {
        font-size:6rem;
    }
}
@media only screen and (max-width: 325px) {
    .Coming-soon-wrapper {
        font-size:4rem;
    }
}